@use "../global/variables.scss";

.photos {
  min-width: 320px;
}

.photos__wrapper {
  min-width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding: 0 20px;

  @media (min-width: variables.$tablet-width) {
    min-width: 90%;
    padding: 0 60px;
  }

  @media (min-width: variables.$desktop-width) {
    min-width: 75%;
    padding: 0;
  }
}

.photos__title {
  margin: 0 auto 24px;

  @media (min-width: variables.$tablet-width) {
    font-size: 30px;
    margin: 0 auto 32px;
  }

  @media (min-width: variables.$desktop-width) {
    padding: 0 40px;
  }
}

.photos__description {
  color: var(--text-secondary);
  text-transform: uppercase;
  margin: 0 auto 36px;
  text-align: center;

  br {
    display: none;
  }

  @media (min-width: variables.$tablet-width) {
    margin: 0 auto 81px;

    br {
      display: block;
    }
  }

  @media (min-width: variables.$desktop-width) {
    margin: 0 auto 89px;
  }
}

.photos__list {
  padding: 24px 0 25px;
  border-top: 1px solid #e5e5e5;
  border-bottom: 1px solid #e5e5e5;

  @media (min-width: variables.$tablet-width) {
    padding: 0;
    border: none;
  }
}
